:root {
    --footer-gap:1rem
}

.footer-container {
    background-color: rgb(242, 242, 242);
    -webkit-box-shadow: inset 0px 2px 16px -5px rgba(0, 0, 0, 0.05);
    -moz-box-shadow: inset 0px 2px 16px -5px rgba(0, 0, 0, 0.05);
    box-shadow: inset 0px 2px 16px -5px rgba(0, 0, 0, 0.05);
}
.main-footer-container {
    display: flex;
    flex-flow: row wrap;
    gap: var(--footer-gap);
    justify-content: space-between;
    max-width: 100%;
    overflow: hidden;
    height: 300px;
    margin-top: 4rem;
    padding-left:var(--general-padding-horiz);
    padding-right:var(--general-padding-horiz);
}
.footer-group:nth-child(1) {
    padding-bottom: 1rem;
}
.main-footer-icon {
    width: 400px;
}

.footer-group:nth-child(1) .footer-last-text {
    margin-top: auto;
    font-size: 1rem;
}

.footer-group {
    position: relative;
    flex: 1;
    display: flex;
    flex-direction: column;
    padding-top: 1.5rem;
    height: 100%;

}
.footer-group:nth-child(1) { /* First group of footer takes up half of the entire horizontal space (twice as much as each of the other ones)*/
    flex: 2;
}


.main-footer-text {
    padding-top:1.5rem ;
    font-size: 1rem;
}
.footer-container h2 {
    font-weight: 300;
    font-family: var(--title-font);
    font-size: 1rem;
    width: 100%;
    padding-left: 1rem;
}

.footer-driver-main-group p {
    font-size: 1rem;
}
.footer-container h3 {
    font-weight: 300;
    font-size: 1rem;
    font-family: var(--title-font);
    color: var(--font-color-dark-primary);
    width: 100%;
    text-align: end;
    padding-right: 2rem;
    margin-bottom: 1.5rem;
    font-weight: 600;
}


.main-footer-container .footer-group:not(:first-child)::after {
    position: absolute;
    top:50%;
    left: calc(-0.5 * var(--footer-gap));
    transform: translateY(-50%);
    height: 90%;
    content: '';
    border: 1px solid rgba(216, 216, 216, .5);

  }
.bottom-footer-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3rem;
    background-color: rgb(216, 216, 216);

}

.footer-driver-main-group {
    display: flex;
    flex-direction: column;
    gap:1rem;
    align-items: end;
    padding-right: 1rem;

}
.footer-driver-main-group > div{
    display: flex;
    height: 24px;
    justify-content: end;
    align-items: center;
    gap: 1rem;
    width: fit-content;
    filter: grayscale(50%);
    pointer-events: all;
    z-index: 1;

}
.footer-driver-main-group > div:hover {
    text-shadow:
    0px 0px 0 var(--font-color-dark-secondary),
    0px 0px 0 var(--font-color-dark-secondary),
    0px 0px 0 var(--font-color-dark-secondary),
    0px 0px 0 var(--font-color-dark-secondary);
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    filter: grayscale(0%);
    cursor: pointer;
}
.footer-driver-main-group > div:hover p {
    color: black;
}


.footer-driver-main-group > div > img {
    max-height: 100%;
    padding-left: 1rem;
}

.bottom-footer-container p {
    font-size: 1rem;
    pointer-events: none;
}

@media screen and (min-width: 460px) {

    #footer-mobile{
        display: none;
    }
    #footer-desktop {
        display: block;
    }


}
@media screen and (max-width: 460px) {
    
    #footer-desktop {
        display: none;
    }

    #footer-mobile{
        display: block;
    }


    #footer-mobile .main-footer-container {
        gap: var(--footer-gap);
        height: fit-content;
        flex-direction: column;
        padding-left:2rem;
        padding-right:2rem;
        padding-bottom: 2rem;
    }

    #footer-mobile .footer-driver-main-group > div > img {
        max-height: 100%;
        padding-left: 1rem;
    }
    #footer-mobile .main-footer-text {
        text-align: center;
    }

    #footer-mobile .main-footer-container .footer-group:not(:first-child)::after {
        position: absolute;
        left:0%;
        top:0%;
        left: calc(-0.5 * var(--footer-gap));
        transform: translateX(0%);
        height: 0%;
        width: 100%;
        content: '';
        border: 1px solid rgba(216, 216, 216, .5);
    
      }
    .main-footer-icon {
        max-width: 80vw;

    }
    #footer-mobile .footer-group h3 {
        font-size: 1rem;

    }

    #footer-mobile .footer-driver-main-group p {
        font-size: 1rem;
    }
}