.driver-modal-container {
    display: flex;
    justify-content: space-between;
    gap: 3vw;
    grid-template-columns: 1fr 2fr; 
    align-items: flex-start;

}
.driver-modal {
    width: 30vw;
    display: flex;
    flex-direction: column;
    padding: 2vw;
    border-radius: .5vw;
    -webkit-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
    -moz-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
    box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
    background-color: white;
    cursor: pointer;
    transition: 300ms;
}
.driver-modal:hover {

    -webkit-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.3);
    -moz-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.3);
    box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.3);
}
.driver-modal > .driver-modal-info-container {
    display: flex;
    justify-content: space-between;
    align-items: end;
}

.driver-modal > .driver-modal-title {
    display: flex;
    justify-content: space-between;
}
.driver-modal-info {
    display: flex;
    flex-direction: column;
    justify-content: end;
}
.driver-modal-info > div{
    display: flex;
    flex-direction: row-reverse;
    gap: .8vw;
    padding-bottom: .8vw;
}
.driver-modal-info > div:last-child {
    padding-bottom: 0rem;
}

.driver-modal > .driver-modal-title > img {
    width: 7.6vw;

}
.driver-modal-info > div > img {
    width: 1.9vw;
}

.driver-modal p {
    font-size: 1.4vw;
    font-family: 'lato';
    font-weight: 300;
}


.driver-modal-title-text > h2 {
    font-family: 'lato';
    font-weight: 300;
    font-size: 2.8vw;
    text-align: end;
}
.driver-modal-title-text > h3 {
    font-family: 'lato';
    font-weight: 300;
    font-size: 1.4vw;
    text-align: end;
}





@media screen and (max-width: 460px) {

    .driver-modal-container {
        flex-direction: column;
        gap:2rem;
        margin-left: -1rem;
        margin-right: -1rem;

    }
    .driver-modal {
        width: calc(100vw - 4rem);
        height: fit-content;
        padding-top: 1rem;
        padding-bottom: 1rem;
        border-radius: .5rem;

        margin-left: 0;
        margin-right: 0;
    }

    .driver-modal > .driver-modal-info-container {
        display: flex;
        justify-content: space-between;
        align-items: end;
 
    }


    .driver-modal > .driver-modal-title > img {
        width: 25vw;
    
    }
    .driver-modal-info-container > div > img {
        width: 6vw;
    }
    .driver-modal p {
        font-size: 4.5vw;
    }
    .driver-modal-title-text > h2 {
        font-size: 8.5vw;
    }
    .driver-modal-title-text > h3 {
        font-size: 4.5vw;

    }

    .driver-modal-info > div > img {
        width: 20px;
        height: 20px;
    }
    
}
