:root {
    --title-font-size:2.2vw;
    --title-font:"Montserrat", sans-serif;
    --title-font-weight:600;
    --description-font-weight:400;
    ---subtitle-font-weight:400;
    --choices-gap:8rem;
    --hero-buttons-v-padding:3rem;
    --hide-tranistion:1000ms;
    --navbar-height:3.5rem;
    
}
.main-lockup-text, .main-lockup-button {
    transition: var(--hide-tranistion);
}
.hero {
    display: grid;
    grid-template-columns:1fr 2fr ;
    margin-top: calc(var(--nav-bar-height) * 2);
    height:  calc( 100% - var(--nav-bar-height));
    padding-left: var(--general-padding-horiz);
    padding-right: var(--general-padding-horiz);
    gap: 3rem;


}
.main-lockup {
    height: fit-content;
    width: fit-content;
    display: grid;
    gap: 2rem;

}

.hero img {
    width: 100%;
    height: auto;
    position: relative;  
}

.main-lockup-text {

    display: flex;
    flex-direction: column;
    gap:2rem
}

.hero h1 {
    font-size: var(--title-font-size);
    font-family: var(--title-font);
    font-weight: var(--title-font-weight);
    color: var(--font-color-dark-primary);

}
.hero h2 {
    font-family: var(--title-font);
    font-size: calc(var(--title-font-size) / 2);
    color: var(--font-color-dark-secondary);
    text-align: left;
    text-decoration: none;
    font-weight: var(--description-font-weight);
}

.hidden {
    background-color: rgba(0, 0, 0, 0);
    opacity: 0.2;
    transition: var(--hide-tranistion);
}

.side-lockup {
    display: flex;
    flex-direction: column;
    gap: 6rem;
}
.side-lockup .logo {
    width: 20rem;
    position: relative;
    left:  calc(50% - 2rem);
    margin-top: -4rem;
    transform: translateX(-50%);

}
.side-title {

    text-align: center;
}
.side-title h2 {
    text-align: center;
    font-size: var( --title-font-size);
    font-weight: var(--title-font-weight);
}



.main-lockup-buttons-container {
    display: flex;
    justify-content: space-around;
    padding-top: 3rem;
}
.main-lockup-button {
    background-color: rgb(34, 34, 34);
    width: 12.8vw;
    height: 3.2vw;
     /* Fixes slight vertical offset on some of the fonts */
    border-radius: 1.6vw;


}

.main-lockup-button a {
    color: rgb(255, 204, 33);
    text-align: center;
    font-size: 1.3rem;
    font-size: 1.4vw;
    line-height: 3vw;
    position: relative;
    top:50%;
    left: 50%;
    display: block;
    transform: translate(-50%, -50%);
    font-family: Arial, Helvetica, sans-serif;

}

.main-lockup-services {
    margin-top: 6rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.main-lockup-services h2 {
    font-size: calc(var(--title-font-size) * .7);
    font-weight: var(--title-font-weight);
    
    color: var(--font-color-dark-primary);
}

.main-lockup-services ul {
    margin-left: -1rem;
}

.main-lockup-services li, p {
    font-family: var(--title-font);
    font-size: calc(var(--title-font-size) / 2);
    color: var(--font-color-dark-secondary);
    text-align: left;
    text-decoration: none;
    font-weight: var(--description-font-weight);
}


.main-lockup-services a {
    color: #0069C2;
}


    
/* MAIN LOCKUP DESKTOP */
@media screen and (min-width: 460px) {
    .mobile-main-lockup {
        display: none;
    }


}






/* MAIN LOCKUP MOBILE */

@media screen and (max-width: 460px) {    
    #hero-title{
        display: none;
    }
    #desktop-side-lockup {
        display: none;
    }
    
    .hero {
        display: flex;
        flex-direction: column;
        margin-top: calc(var(--nav-bar-height) + 1rem);
        padding-left: 0;
        padding-right:0;
        margin-left: var(--mobile-padding-horiz);
        margin-right: var(--mobile-padding-horiz);
        gap: 1rem;
    }
    .mobile-main-lockup {

        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
    .mobile-main-lockup h1 {
        font-size: 1.5rem;

        text-align: center;
    }
    .mobile-main-lockup h2 {
        font-size: 1rem;
        text-align: center;
    }
    .main-lockup-text {
        gap: .5rem;
        margin-bottom: 2rem;
    }
    .driver-modal-container {
        padding-bottom: 4rem;
    }
    .driver-modal-container > * {
        align-self: center;
        
    }

    .main-lockup-services {
        margin-top: 0;
    }
    .main-lockup-services h2 {
        font-size: 1.4rem;
        text-align: left;
    }
    .main-lockup-services li {
        text-align: left;

        margin-left: -.5rem;
        font-size: 1rem;
    }
    .main-lockup-services p {
        font-size: 1rem;
    }
  }











