:root {
    --contacts-photo-horiz-margin:1rem;

}


.contacts-container {
    margin-top: var(--nav-bar-height);
    height: calc(100vh - var(--nav-bar-height));
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap:4rem;
    padding-left: 4rem;
    padding-right: 4rem;

}
.contacts-drivers-container {
    display: flex;
    flex-direction: column;
    gap: 2rem;

}
.contacts-photo {
    width:55%;
    height:70%;
    display: flex;
    flex-direction: column;
    justify-content: space-between  ;
    position: relative; 
    overflow: hidden;  
    border-radius: .5rem;
    -webkit-box-shadow: var(--soft-shadow);
    -moz-box-shadow: var(--soft-shadow);
    box-shadow: var(--soft-shadow);
}
.contacts-photo img {
    height: 100%; 
    width: 100%;  
    object-fit: cover; 
    position:absolute;
    object-position: 50% 100%;
    z-index: -1;
}
.contacts-photo-text-div {
    display: flex;
    background-color: white;
    height: 4vw;
    justify-content: center;
    align-items: center;
    width: fit-content;
    padding-left: 4vw;
    padding-right: 4vw;
    margin:var(--contacts-photo-horiz-margin);
    position: relative;
    left: 100%;
    transform: translateX(calc(-100% - 2 * var(--contacts-photo-horiz-margin)));
    border-radius: .5vw;
    -webkit-box-shadow: var(--soft-shadow);
    -moz-box-shadow: var(--soft-shadow);
    box-shadow: var(--soft-shadow);
}
.contacts-photo-text-div p {
    font-weight: bold;
    font-size: 1.8vw;

}
.contacts-photo-loaction-div {
    display: flex;
    background-color: white;
    height: 4vw;
    justify-content: center;
    align-items: center;
    width: fit-content;
    padding-left: 2vw;
    padding-right: 4vw;
    margin:var(--contacts-photo-horiz-margin);
    gap:2rem;
    border-radius: .5vw;
    -webkit-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
    -moz-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
    box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
}
.contacts-photo-svg {
    height: 50%;
    width:fit-content;
    
}
.contacts-photo-loaction-div p {
    font-size: 28px;
}

@media screen and (max-width:460px) {

    
    .contacts-container {
        margin-top: calc(var(--nav-bar-height) + 2rem);
        height: auto;
        flex-direction: column;
        gap:4rem;
        padding-left: 0;
        padding-right: 0;
        padding-bottom: 3rem;
    }
    
    .contacts-photo {
        width: calc(100% - 4rem);
        height:200px;
        margin-left: 2rem;
        margin-right: 2rem;
    }




    .contacts-photo-text-div {
        display: flex;
        background-color: white;
        height: 2rem;
        padding-left: 1rem;
        padding-right: 1rem;
        margin:var(--contacts-photo-horiz-margin);
        border-radius: .5rem;
    }
    .contacts-photo-text-div p {
        font-weight: bold;
        font-size: 1rem;
    }
    .contacts-photo-loaction-div {
        display: flex;
        background-color: white;
        height: 1.5rem;
        justify-content: center;
        align-items: center;
        width: fit-content;
        padding-left: 1rem;
        padding-right: 1rem;
        margin:var(--contacts-photo-horiz-margin);
        gap:2rem;
        border-radius: .5vw;
        -webkit-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
        -moz-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
        box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
    }
    .contacts-photo-svg {
        height: 50%;
        width:fit-content;
        
    }
    .contacts-photo-loaction-div p {
        font-size: .8rem;
    }


}