img {
    pointer-events: none;
}
.hero-videos {
    pointer-events: none;
}
/* Top Image with Name */
.hero-container {
    width: 100%;
    min-height: 100vh;
    z-index: -1;
    background-color: rgba(0, 0, 0, 0);
}

.hero-container img {
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
}

.hero-container video {
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
}
.layer--1 {
    z-index:-1
}
.layer-2 {
    z-index: 2;
}
video {
    transition: var(--hide-tranistion) ease;
  }
video.hidden {
    opacity: 0; 
  }
  
  video.visible {
    opacity: 1; 
  }