:root {
    --map-height:550px;

}

.main-lockup-map-container {
    display: flex;
    border-radius: .5vw;
    -webkit-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
    -moz-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
    box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
    transition: 300ms;
}

.main-lockup-map-container:hover {
    -webkit-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.3);
    -moz-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.3);
    box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.3);
}

.main-lockup-map-title {
    position: absolute;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    z-index: 1  ;
    background-color: white;
    margin: .6rem;
    padding-left: .8rem;
    padding-top: .6rem;
    padding-bottom: .8rem;
    width: 25vw;
    height: calc(var(--map-height) * 0.3);
    border-radius: .5rem;
    -webkit-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
    -moz-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
    box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
}

.main-lockup-map-title h3 {
    padding-bottom:.2rem;
    font-size: 1.2rem;
    color: var(--font-color-dark-primary);
}
.main-lockup-map-title p {
    font-size: .9rem;
    color: var(--font-color-dark-secondary);
}
.main-lockup-map-title a {
    font-family: var(--title-font);
}

/* Style for the map container */
.map-component-container {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    
  }

.map-component-container iframe {
    width: 100%;
    height: var(--map-height);
    filter: grayscale(50%);
    border: 10px;
    align-self: flex-end;
    border-radius: .5vw;
} 
iframe + div { border:none!important; 
opacity: 0 !important;}
.map-component-container iframe:focus {
    border: 0 !important;
} 
#minimap div { border:none!important; outline:none; }



