:root {
    --title-font-size:2.4rem;
    --h2-font-size:1.5rem;
    --description-font-size:1.2rem;
    --title-font:"Montserrat", sans-serif;
    --title-font-weight:600;
    --description-font-weight:400;
}


.services-container {
    margin-top:calc(var(--nav-bar-height) + 5rem) ;
    margin-bottom: 15rem;
    margin-left: var(--general-padding-horiz);
    margin-right: var(--general-padding-horiz);
    padding-left: 12vw;
    padding-right: 12vw;
    display: flex;
    flex-direction: column;
    gap:2rem
}

.services-title h1 {
    font-size: var(--title-font-size);
    font-family: var(--title-font);
    font-weight: var(--title-font-weight);
    color: var(--font-color-dark-primary);
    padding-bottom: 1rem;

}
.services-title p {
    font-family: var(--title-font);
    font-size: calc(var(--title-font-size) / 2);
    color: var(--font-color-dark-secondary);
    text-align: left;
    text-decoration: none;
    font-weight: var(--description-font-weight);
    padding-bottom: 2rem;
}
.services-group {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.services-group h2 {
    font-size: var(--h2-font-size);
    font-family: var(--title-font);
    font-weight: var(--title-font-weight);
    color: var(--font-color-dark-primary);
}
.services-group p {
    font-size: var( --description-font-size);
    font-family: var(--title-font);
    font-weight: var(--description-font-weight);
    color: var(--font-color-dark-secondary);
    padding-bottom: .5rem;
}
.services-group li {
    margin-left:-1rem;
    font-family: var(--title-font);
    font-size: var( --description-font-size);
    font-family: var(--title-font);
    font-weight: var(--description-font-weight);
    color: var(--font-color-dark-secondary);
}
.services-group ul {
    padding-bottom: 1rem;
}


@media screen and (max-width: 460px) {

    .services-container {
        margin-top:calc(var(--nav-bar-height) + 1rem) ;
        margin-bottom: 15rem;
        margin-left: var(--mobile-padding-horiz);
        margin-right: var(--mobile-padding-horiz);
        padding-left: 0;
        padding-right: 0;
        margin-bottom: 0;
    }

    .services-title h1 {
        font-size: var(--mobile-title-font-size);
    }
    .services-title p {
        font-size: var(--mobile-description-size);
    }
    .services-group h2 {
        font-size: var(--mobile-description-size);
    }
    .services-group p {
        font-size: var(--mobile-description-size);
    }
    .services-group li {
        font-size: var(--mobile-description-size);
    }

}