@import url('https://fonts.googleapis.com/css2?family=Figtree:wght@300;400&display=swap');

:root {
    --padding-horizontal-nav-elements: 1rem;
    --margin-horizontal-nav-elements: 1rem;
    --button-border-radius: 10rem;
    --nav-bar-font-family: 'Figtree', sans-serif;
    --nav-grid-gap: 2.25rem;
    --nav-bar-height: 5rem;
    --nav-blur: blur(5px);
    --hover-underline-color: #121212;
    --nav-bar-padding-horiz:var(--general-padding-horiz);
}

.main-nav {
    z-index: 3;
    position: fixed;
    top: 0;
    height: var(--nav-bar-height);
    padding-left: var( --nav-bar-padding-horiz);
    padding-right: var( --nav-bar-padding-horiz);
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    width: 100%;
    transition: 500ms;

}
.main-nav a {
    text-decoration: none;
}

.main-nav img {
    max-height: 4rem;
}

.main-nav>div {
    display: flex;
    justify-content:center;
    height: calc(var(--nav-bar-height) * 0.8);
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    border-radius: var(--button-border-radius);
    
    
}

.main-nav .nav-left {
    width: auto;

}

.main-nav .nav-center {
    display: grid;
    grid-auto-flow: column;
    gap: var(--nav-grid-gap);
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
}

.nav-center a {
    padding-left: var(--padding-horizontal-nav-elements);
    padding-right: var(--padding-horizontal-nav-elements);
}


.main-nav a {
    font-family: var(--nav-bar-font-family);
    color: rgba(0, 0, 0, .8);
    font-size: 1.1rem;
    border-radius: var(--button-border-radius);
    display: flex;
    align-items: center;
}
    /* HOVERING CODE */
    

.nav-left a:hover {
    margin-left: calc(0rem - var(--padding-horizontal-nav-elements));
    margin-right: calc(0rem - var(--padding-horizontal-nav-elements));
    padding-left: var(--padding-horizontal-nav-elements);
    padding-right: var(--padding-horizontal-nav-elements);
}

.nav-right a:hover {
    margin-left: calc(0rem - var(--padding-horizontal-nav-elements));
    margin-right: calc(0rem - var(--padding-horizontal-nav-elements));
    padding-left: var(--padding-horizontal-nav-elements);
    padding-right: var(--padding-horizontal-nav-elements);
}
    
    /* HOVERING LINE CODE */
.main-nav>div> * {
    position: relative; /* Have to set position to relative */
}


    
.hover-underline-animation {
    display: inline-block; /* Display inline block */
    position: relative; /* Set position relative */
    color: var(--hover-underline-color) /* color of underline */
}

.main-nav>div> *:after {
    content: ''; /* Add content after the element */
    position: absolute;
    width: 100%; /* Full width */
    transform: scaleX(0); /* Initial scale of 0 for underline effect */
    height: 2px; /* height after animation*/
    bottom: 10px; 
    left: 0; 
    background-color: var(--hover-underline-color) ;
    transform-origin: bottom center; 
    transition: transform 0.25s ease-out; 
}

.main-nav>div> *:hover:after {
    transform: scaleX(1); /* Scale the underline on hover */
    transform-origin: bottom center;
    height: 2px; /* height during animation */
}


.main-nav>.nav-right> *:after {

display: none;
}


    /* DROPDOWN CODE */
.dropdown {
    position: relative;
    display: block;
}

.dropdown .dropdown-content {
    margin-left: calc(0rem - var(--padding-horizontal-nav-elements));
    margin-right: calc(0rem - var(--padding-horizontal-nav-elements));
    margin-top: calc(var(--nav-bar-height) * 0.8);
    display: none;
    position: absolute;
    top: 0;
    z-index: 3;
    gap: calc(var(--nav-bar-height) * 0.2);
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 0 var(--button-border-radius) var(--button-border-radius) var(--button-border-radius);
}

.dropdown .dropdown-content>* {
    display: flex;
    height: calc(var(--nav-bar-height) * 0.8);
    position: relative;
    border-radius: var(--button-border-radius);
}

.dropdown:hover .dropdown-content {
    display: grid;
}

.dropdown .dropdown-content {
    margin-left: calc(0rem - var(--padding-horizontal-nav-elements));
    margin-right: calc(0rem - var(--padding-horizontal-nav-elements));
    margin-top: calc(var(--nav-bar-height) * 0.8);
}

.main-nav .dropdown:hover {
    transition: 20ms;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: var(--button-border-radius) var(--button-border-radius) 0 0;

}
    /* SCROLLING CODE */
.nav-scrolled {
    background-color: rgba(255, 255, 255) !important;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.15);
    backdrop-filter: none;
}
.nav-scrolled .nav-left {
    opacity: 1 !important;
    visibility: visible;
}
 

.main-nav .nav-backdrop {
    position: absolute;
    top: 0;
    left: 0;
    height: var(--nav-bar-height);
    width: 100%;
    display: flex;
    transform: none;
    border-radius: 0;
/*    backdrop-filter: var(--nav-blur); */
}

@media screen and (min-width: 460px) {

    #nav-mobile {
        display: none;
    }
    #nav-desktop {
        display: grid;
    }
        
    }


@media screen and (max-width: 460px) {


    #nav-desktop {
        display: none;
    }


    #nav-mobile {
        display: grid;
        padding-left: 2rem;
        padding-right: 2rem;


    }


    #nav-mobile .nav-left img {
        align-self: center;
    }

    #nav-mobile .nav-right {
        margin-right: -.8rem;

    }
    #nav-mobile img {
        max-height: 2rem;
    }
}