

.curtain-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgb(216, 216, 216);
    z-index: 10;
    display: flex;
    visibility: hidden;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease;
    transform: translateX(-100%);
  }

  .curtain-menu.open {
    visibility: visible;
    transform: translateX(0);
  }


  .close-btn {
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 2rem;
    color: var(--font-color-dark-secondary);
    background: none;
    border: none;
    cursor: pointer;
  }

  .menu-items {
    list-style: none;
    padding: 0;
    margin: 0;
    text-align: center;
  }

  .menu-items li {
    margin: .5rem 0;
    padding: 1rem;

  }
  
  .menu-items a {
    color: var(--font-color-dark-primary);
    font-family: var(--title-font);
    text-decoration: none;
    font-size: 1.5rem;
  }


  
@media screen and (min-width:460px) {
    .curtain-menu {display:none}

}