
:root {
    --h1-font-size:3.4rem;
    --h2-font-size:1.4rem;
    --pricing-font-size:1.4rem;
    --title-font:"Montserrat", sans-serif;
    --title-font-weight:600;
    --description-font-weight:400;
}

.pricing-container {
    margin-top: var(--nav-bar-height);
    margin-left: var(--general-padding-horiz);
    margin-right: var(--general-padding-horiz);

}
.pricing-text > h1{
    font-size: var(--h1-font-size);
    font-family: var(--title-font);
    font-weight: var(--title-font-weight);
    color: var(--font-color-dark-primary);
    padding-bottom: 1rem;

}
.pricing-text > p{
    font-size: var(--pricing-font-size);
    font-family: var(--title-font);
    font-weight: var(--description-font-weight);
    color: var(--font-color-dark-secondary);
    padding-bottom: .5rem;
}


.pricing-additional-info {
    margin-top:calc(var(--nav-bar-height) + 5rem) ;
    margin-bottom: 15rem;
    margin-left: var(--general-padding-horiz);
    margin-right: var(--general-padding-horiz);
    display: flex;
    flex-direction: column;
    gap:1rem
}
.pricing-additional-info h2 {
    font-size: var(--h1-font-size);
    font-family: var(--title-font);
    font-weight: var(--title-font-weight);
    color: var(--font-color-dark-primary);
    padding-bottom: 1rem;
}
.pricing-additional-info li {
    margin-left:-1rem;
    margin-bottom: .2rem;
    font-family: var(--title-font);
    font-size: var(--pricing-font-size);
    font-family: var(--title-font);
    font-weight: var(--description-font-weight);
    color: var(--font-color-dark-secondary);
}
.pricing-additional-info > p {
    font-size: var(--pricing-font-size);
    font-family: var(--title-font);
    font-weight: var(--description-font-weight);
    color: var(--font-color-dark-secondary);
    padding-bottom: .5rem;
}

  @media screen and (min-width: 2000px) {

    .pricing-container {
        padding-left: 12vw;
        padding-right: 12vw;
    }
    .pricing-additional-info {
        padding-left: 12vw;
        padding-right: 12vw;
    }





    
  }



  @media screen and (max-width: 460px) {

    .pricing-container {
        margin-top: calc(var(--nav-bar-height) + 1rem);
        margin-left: var(--mobile-padding-horiz);
        margin-right: var(--mobile-padding-horiz);
    }

    .pricing-text > h1{
    font-size: var(--mobile-title-font-size);
    padding-bottom: 1rem;

    }
    .pricing-text > p{
        font-size: var(--mobile-description-size);
        padding-bottom: .5rem;
    }
    .pricing-additional-info {
        margin-top:2rem;
        margin-bottom: 4rem;
        margin-left: var(--mobile-padding-horiz);
        margin-right: var(--mobile-padding-horiz);
        gap:1rem
    }

    .pricing-additional-info h2 {
        font-size: var(--mobile-title-font-size);
        font-family: var(--title-font);
        font-weight: var(--title-font-weight);
        color: var(--font-color-dark-primary);
        padding-bottom: 1rem;
    }
    .pricing-additional-info li {
        margin-left:-1rem;
        margin-bottom: .2rem;
        font-size: var(--mobile-description-font-size);
    }
    .pricing-additional-info > p {
        font-size: var(--mobile-description-size);
        font-family: var(--title-font);
        font-weight: var(--description-font-weight);
        color: var(--font-color-dark-secondary);
        padding-bottom: .5rem;
    }
    
  }




