:root {
    --main:1rem;
    --fare-button-height:3rem;
}


.fare-modal-container {
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    gap:1rem;

}
.fare-modal {
    width: 360px;
    background-color: white;
    border-radius: .5rem;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    -webkit-box-shadow: 0px 13px 46px -18px rgba(0,0,0,0.19);
    -moz-box-shadow: 0px 13px 46px -18px rgba(0,0,0,0.19);
    box-shadow: 0px 13px 46px -18px rgba(0,0,0,0.19);
}
.fare-title {
    font-size: 32px;
    text-align: center;
    font-weight: 500;
    
}
.fare-price {
    font-size:32px;
    font-weight: 800;
}
.fare-price-ending {
    font-size: calc(36px / 2);
    font-weight: 700;
    padding-left: 5px;
}
.fare-description {
    font-size: 18px;
}
.fare-button-container {
    display: flex;
    justify-content: space-between;
    padding-top: 1rem;
    margin-top: auto;
}
.fare-button {
    display: flex;
    width: 10rem;
    height: var(--fare-button-height);
    background-color: rgb(34, 34, 34);
    border-radius:var(--fare-button-height) ;
    justify-content: center;
    align-items: center;
  }
  .fare-button > a {
    margin-top: -2px ;
    color:#FFCC21;
    font-weight: 500;
  }


  



  @media screen and (max-width: 1850px) {


    .fare-description {
        font-size: 16px;
    }


    .fare-modal {
        width: 280px;
    }

    .fare-button {
        width: 8rem;
    }

    .fare-title {
        font-size: 28px;
    }

    .fare-price {
        font-size: 28px;
    }
  }



  
  @media screen and (max-width: 460px) {

    .fare-modal-container {
        margin-top: 2rem;
        flex-direction: column;
        gap:1rem;
    }

    .fare-modal {
        width: 100%;
        background-color: white;
        border-radius: .5rem;
        padding: 2rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        -webkit-box-shadow: 0px 13px 46px -18px rgba(0,0,0,0.19);
        -moz-box-shadow: 0px 13px 46px -18px rgba(0,0,0,0.19);
        box-shadow: 0px 13px 46px -18px rgba(0,0,0,0.19);
    }




    .fare-title {
        font-size: var(--mobile-title-font-size)
        
    }

    .fare-price {
        font-size:var(--mobile-title-font-size);
        font-weight: 800;
    }
    .fare-price-ending {
        font-size: calc( var(--mobile-title-font-size) * .6);
        padding-left: 3px;
    }
    .fare-description {
        font-size: var(--mobile-description-font-size);
    }

    
    .fare-button-container {
        display: none;
    }



  }