/* Style for the map container */
.map-component-container {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;

  }

.map-component-container iframe {
    width: 100%;
    height: 600px;
    filter: grayscale(50%);
    border: 10px;
    align-self: flex-end;
} 
iframe + div { border:none!important; 
opacity: 0 !important;}
.map-component-container iframe:focus {
    border: 0 !important;
} 
#minimap div { border:none!important; outline:none; }



/* */

@media screen and (max-width:460px) {



    .map-component-container {
        width: 100%;
        height: 100%;
        overflow: hidden;
        display: flex;
        padding-left: 2rem;
        padding-right: 2rem;
        padding-bottom: 4rem;
        padding-top: 1rem;
        
      }


    .map-component-container iframe {
        width: 100%;

        border-radius: 1rem;
        height: 400px;
        filter: grayscale(50%);
        -webkit-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
        -moz-box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
        box-shadow: 21px 21px 41px 1px rgba(0,0,0,0.19);
        

    } 

    
}