:root {
    --primary-color: #FFCC21;
    --secondary-color:rgba(68, 168, 190, 1);
    --font-dark:rgb(77,81,86);
    --main-font-family:"Montserrat", sans-serif;
    --title-font:"Montserrat", sans-serif;
    --font-color-dark-primary:#0D0D0D;
    --font-color-dark-secondary:#262626;
    --font-color-white-primary:#E6E6E6;
    --border-radius-button:0.8rem;
    --box-shadow:0 2px 4px 0 rgba(0,0,0,.15);
    --general-padding-horiz:4rem;
    --mobile-padding-horiz:2rem;
    --mobile-title-font-size:1.5rem;
    --mobile-description-size:1rem;
    --soft-shadow:21px 21px 41px 1px rgba(0,0,0,0.19);
}
* {
    margin: 0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-decoration: none;
  }
body {
  background-color: #f8f8f8;
}
a {
  color: var(--font-color-dark-primary);
}
.navBar-height {
height: var(--nav-bar-height);
}
.nav-bar-dummy {
  height: var(--nav-bar-height);
}
.test{
  font-size: 10rem;
  
}
.font-accent-color {
  color: var(--primary-color);
}



/* HOVERING LINE CODE */
.custom-link {
  position: relative; /* Have to set position to relative in order to ensure good scaling to text */
  color: #0069C2 !important;
  font-family: var(--title-font);
}


.custom-link:after {
  content: ''; /* Add content after the element */
  position: absolute;
  width: 100%; /* Full width */
  transform: scaleX(0); /* Initial scale of 0 for underline effect */
  height: 1px; /* height or size after animation*/
  bottom: 0px; 
  left: 0; 
  background-color:#005aa8; /* defines colour of the line (for whatever reason looks mor accurate when is darker than actual text if blue*/
  transform-origin: bottom center; 
  transition: transform 0.25s ease-out; 
}

.custom-link:hover:after {
  transform: scaleX(1); /* Scale the underline on hover */
  transform-origin: bottom center;
  height: 1px; /* height during animation */
}