



.icons-container {
    display: flex;
    gap: .5rem;
    width: 200px;
    height: fit-content;
    align-items: center;

}

.icons-container > img {
    width: 1.8vw;
    height: 1.8vw;
    filter: grayscale(60%) brightness(80%) contrast(0.5);

    pointer-events: auto;
    transition: all 0.3s ease-in-out;
}

.icons-container > img:hover {


    filter: grayscale(0%)   ;

    }
    
    @media screen and (max-width: 460px) {

        .icons-container {
 
            width: 40%;

        }
        
        .icons-container > img {
            width: 1.8vw;
            height: auto;
            filter: grayscale(50%) ;

        }
    }